import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Routes, Router } from '@angular/router';
import { Location } from '@angular/common';
import { FlatpickrOptions } from 'ng2-flatpickr';
import { FormGroup, FormBuilder, Validators, FormArray, FormControl } from '@angular/forms';
import { PickListService } from 'src/app/service/pick-list.service';
import { DataService } from 'src/app/service/data.service';
import { DATEFORMAT } from 'src/app/messages';

@Component({
  selector: 'app-generate-pick-list',
  templateUrl: './generate-pick-list.component.html'
})
export class GeneratePickListComponent implements OnInit {

  PageTitle = "Genrate PickList ";
  exampleOptions: FlatpickrOptions = {
    defaultDate: new Date(),
    dateFormat	:DATEFORMAT,
    noCalendar: true,
    //enableTime: true
  };

  viewId: string;
  objectArray: any;
  orderDetails: any = {};
  productList: any = [];
  addForm: FormGroup;
  showLoader: boolean;
  currentCompany:any;
  date_format:string="M/d/yy";
  timezone:string
  time_format:string;

  constructor(
    private activatedRoute: ActivatedRoute,
    private pickListService: PickListService,
    private location: Location,
    private fb: FormBuilder,
    private router: Router,
    private dataService:DataService
  ) {


    this.addForm = this.fb.group({
      picked_by: [null, Validators.compose([Validators.required])],
      notes: [null],
      unload_detail: this.fb.array([]),
    });

    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
  }

  ngOnInit() {
    this.dataService.currentCompany.subscribe((response)=>{
      if(response){
      this.timezone=String(response.timezone.format);;
      this.time_format=response.time_format.label;
      this.date_format=response.date_format.label;
      this.currentCompany=response;
      }
      
    });
    this.getObject();
  }

  back() {
    this.location.back();
  }

  getObject() {
    this.pickListService.getPickListReportsDetails(this.viewId).subscribe((response) => {
      if (response.success) {
        this.orderDetails = response.data.order_detail;
        this.productList = response.data.product;
        this.setData();
        // this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.orderDetails = {};
        this.productList = [];
      }
    }, (error) => {
      this.orderDetails = {};
      this.productList = [];

    });
  }
  setData() {
    this.addForm.patchValue({
      notes: this.orderDetails.notes,
      picked_by: this.orderDetails.picked_by,

    });
    if (this.productList.length > 0) {
      const control = <FormArray>this.addForm.get('unload_detail');
      for (let i = 0; i <= control.length + 1; i++) {
        control.removeAt(i);
      }

      this.productList.forEach((obj, i) => {
        control.push(this.fb.group({
          pick_list_id: obj.pick_list_id,
          sku_no: obj.sku_no,
          product_name: obj.product_name,
          product_qty: obj.product_qty,
        })
        );
      });
    }

  }


  submitForm(formData: any): void {
    if (formData.valid) {
      this.showLoader = true;
      const data = new FormData();
      data.append('picked_by', formData.value.picked_by);
      // data.append('unload_detail', JSON.stringify(formData.value.unload_detail));
      // data.append('notes', formData.value.notes ? formData.value.notes : '');
      this.pickListService.getPickListSubmitReports(this.viewId, data).subscribe((response) => {
        this.showLoader = false;
        if (response.success) {
          this.back();
        }
      }, (error) => {
        this.showLoader = false;
      });
    }
  }


  addNewRow() {
    const control = <FormArray>this.addForm.get('unload_detail');
    control.push(this.initRows());
  }

  initRows() {
    return new FormGroup({
      pick_list_id: new FormControl(''),
      sku_no: new FormControl(''),
      product_name: new FormControl(''),
      product_qty: new FormControl(0, Validators.compose([Validators.required])),
    });
  }

}
